import React from 'react';
import './ImageLinkForm.css';

export default function ImageLinkForm({ onInputChange, onButtonSubmit }) {
	return (
		<div>
			<p className="f3">{'This magic brain will detect faces in your pictures. Give it a try.'}</p>
			<div className="center">
				<div className="center pa4 br3 shadow-5 form">
					<input onChange={onInputChange} className="f4 pa2 w-70 center" type="text" name="url" id="url" />
					<button
						onClick={onButtonSubmit}
						className="form-btn w-30 grow f4 link ph3 pv2 dib white bg-light-purple"
					>
						Detect
					</button>
				</div>
			</div>
		</div>
	);
}
