import React, { Component } from 'react';
import Clarifai from 'clarifai';
import Navigation from './components/Navigation';
import Particles from 'react-particles-js';
import Rank from './components/Rank';
import ImageLinkForm from './components/ImageLinkForm';
import FaceRecognition from './components/FaceRecognition';
// import Test from './components/Test';
import './App.css';

const app = new Clarifai.App({
	apiKey : '372d2b5f35924c8aa42adba1f8961f59'
});

export default class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			input : '',
			url   : '',
			boxArray   : []
		};
	}

	calculateFaceLocation = data => {
		const faces = data.outputs[0].data.regions.map(face => face.region_info.bounding_box);
		console.log('faces:', faces);

		const image = document.querySelector('#inputImage');
		const width = Number(image.width);
		const height = Number(image.height);
		
		return faces.map(face => ({
			leftCol   : face.left_col * width,
			topRow    : face.top_row * height,
			rightCol  : width - face.right_col * width,
			bottomRow : height - face.bottom_row * height
		}));
	};

	displayFaceBox = boxes => this.setState(() => ({ boxArray: boxes }));

	onInputChange = event => {
		let value = event.target.value;

		this.setState(() => ({
			input : value
		}));
	};

	onButtonSubmit = () => {
		this.setState(() => ({
			url : this.state.input
		}));

		document.querySelector('#url').value = '';

		app.models
			.predict(Clarifai.FACE_DETECT_MODEL, this.state.input)
			.then(response => this.displayFaceBox(this.calculateFaceLocation(response)))
			.catch(err => console.log(err));
	};

	render() {
		const particlesStyle = {
			particles : {
				number : {
					value   : 50,
					density : {
						enable     : true,
						value_area : 800
					}
				}
			}
		};

		return (
			<div className="App">
				<Particles className="particles" params={particlesStyle} />
				<Navigation />
				<Rank />
				<ImageLinkForm onInputChange={this.onInputChange} onButtonSubmit={this.onButtonSubmit} />
				<FaceRecognition url={this.state.url} box={this.state.boxArray} />
			</div>
		);
	}
}
