import React from 'react';
import Logo from './Logo';

export default function Navigation() {
	return (
		<nav className="ma4 pointer" style={{ display: 'flex', justifyContent: 'space-between' }}>
			<Logo />
			<p className="f3 gold link dim pa3 pointer">Made by Radu &copy; {new Date().getFullYear()}</p>
		</nav>
	);
}
